// 1. Normalize

html {
  box-sizing: border-box;
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{
    @include body-text;
    -webkit-font-smoothing : antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: $black;
    overflow: hidden;
    background-color: $white;
}

a:not([href]):not([tabindex]){
    cursor: pointer;
}

a {
    text-decoration: none;
    color: inherit;
    &:hover {
      color          : inherit;
      text-decoration: none;
    }
}
  
li{
    list-style: none;
}

ul{
    padding-left: 0;
    margin-bottom: 0;
}

::placeholder {
    color: $placeholder-color;
}

.h1,
.h1,
.h2,
.h3,w
.h4,
.h5,
.h6,
a,
h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
    margin-bottom: 0;
}

figure{
    margin: 0;
}

button{
    border: none;
}

input{
    appearance: none;
    background-color: transparent;
    outline: none;
    border: none;
}


.lenis.lenis-smooth {
    scroll-behavior: auto !important;
  }
  
  .lenis.lenis-smooth [data-lenis-prevent] {
    overscroll-behavior: contain;
  }
  
  .lenis.lenis-stopped {
    overflow: hidden;
  }
  
  .lenis.lenis-scrolling iframe {
    pointer-events: none;
  }

// 2. Pages
// 3.1 Home Page
// 3.1.1 Video Section

.video-container{
    transition: height 1s ease-in-out;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;

    .cursor-expand{
        display: none;
    }

    @include media-breakpoint-up(lg){
        .cursor-expand{

            display: block;
            
            &::after{
                content: url("data:image/svg+xml,%3Csvg width='44' height='44' viewBox='0 0 44 44' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M29.5 1.375H42.625V14.5M41.4262 2.57031L27.625 16.375M14.5 42.625H1.375V29.5M2.57383 41.4297L16.375 27.625' stroke='%23F4F3F0' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
            }

            &.in{
                &::after{
                    content: url("data:image/svg+xml,%3Csvg width='49' height='49' viewBox='0 0 49 49' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M29 46.5V29M29 29H46.5M29 29L47.4062 47.4016M21 3.5V21H3.5M19.4062 19.4016L1 1' stroke='%23F4F3F0' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
                }
            }
        }
    }

    .video{
        width: 100%;
        height: 100%;
        background: $blue;
    }
}

.expand-button{
    position: absolute;
    display: inline-block;
    bottom: 30px;
    right: 15px;
    mix-blend-mode: difference;
    .reduce{
        display: none;
    }
    &.open{
        .expand{
            display: none;
        }
        .reduce{
            display: block;
        }
    }
    @include media-breakpoint-up(md){
        right: auto;
        bottom: 90px;
        left: 50%;
        transform: translateX(-50%);
    }
    @include media-breakpoint-up(lg){
        display: none;
    }
    @include media-breakpoint-up(md){
        .expand{
            width: 45px;
            height: 45px;
            path{
                stroke-width: 1.4px;
            }
        }
        .reduce{
            width: 46.406px;
            height: 46.402px;
            path{
                stroke-width: 1.4px;
            }
        }
    }
}

// 3.2 About Page
// 3.2.1 Description Section

.pre-desc{
    figure{
        display: inline-block;
        vertical-align: top;
        position: relative;
        transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
        overflow: hidden;
        bottom: 5px;
        height: 30px;
        @include media-breakpoint-up(md){
            height: 40px;
        }
        @include media-breakpoint-up(lg){
            bottom: 10px;
            height: 60px;
        }
        img{
            height: 100%;
            display: block;
            transition: transform 1.2s cubic-bezier(0.215, 0.61, 0.355, 1);
            transition-delay: 0.1s;
        }
        &:hover{
            z-index: 2;
            transform: scale3d(2.7, 2.7, 2.7);
            img{
                transform: scale3d(1.12, 1.12, 1.12);
            }
        }
    }
}

// 3.2.2 Archive Section

.archivos-links{
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 10px;
    @include media-breakpoint-up(md){
        text-align: start;
        gap: 18px;
    }
    @include media-breakpoint-up(lg){
        gap: 20px;
    }
}

.text-archive{
    text-align: center;
    @include media-breakpoint-up(md){
        text-align: start;
    }
}

// 3.2.3 Images Section
#presentacion-images{
    .left-images{
        padding-right: 5px;
        @include media-breakpoint-up(md){
            padding-right: 0;
        }
        @include media-breakpoint-up(lg){
            padding-right: 15px;
        }
        figure{
            &:not(:first-child){
                margin-top: 10px;
                @include media-breakpoint-up(md){
                    margin-top: 45px;
                }
                @include media-breakpoint-up(lg){
                    margin-top: 90px;
                }
            }
        }
    }
    
    .right-images{
        padding-left: 5px;
        @include media-breakpoint-up(md){
            padding-left: 0;
        }
        @include media-breakpoint-up(lg){
            padding-left: 15px;
        }
        figure{
            &:not(:first-child){
                @include media-breakpoint-down(md){
                    margin-top: 10px;
                }
            }
            @include media-breakpoint-up(md){
                margin-top: 40px;
            }
            @include media-breakpoint-up(lg){
                margin-top: 90px;
            }
        }
    }
}

// 3.3 Info Page
// 3.3.1 Manifesto Section

.manifesto{
    &-text{
        margin-left: auto;
        margin-right: auto;
        color: $green;
        h2{
        text-align: center;
        }
    }
    &-img{
        margin: 0 auto;
        @include media-breakpoint-up(lg){
            margin: 0;
        }
    }
}

// 3.3.2 Unete Section

.unete-container{
    background-color: $off-white;
    padding: 45px 30px 55px;
    @include media-breakpoint-up(md){
        padding: 63px 60px 60px;
    }
    @include media-breakpoint-up(lg){
        padding: 65px 110px 60px;
    }
    display: flex;
    flex-direction: column;
    h3{
        text-align: center;
    }
}

//3.5. Foto Page
//3.5.1 Principal Fotos Section

.folio-container{
    display: inline-flex;
    align-items: start;
    gap: 15px;
    justify-content: center;
    width: 100%;
    .folio-num{
        color: $blue;
        transform: rotate(-2deg);
    }
}

#principal-fotos{
    .left-images{
        .container-text{
            @include media-breakpoint-up(md){
                position: sticky;
                top: 0;
            }
        }
    }
    .right-images{
        .img-wrapper{
            &:not(:first-child){
                margin-top: 30px;
            }
        }
    }
}

//3.5.2 Fotos Creditos Section

.colab-fotos{
    color: $red;
}

.cred-fotos{
    color: $red;
}

.cred-container{
    display: flex;
    padding-bottom: 8px;
    @include media-breakpoint-up(lg){
        padding-bottom: 3px;
    }
}
.point-line{
    flex: 1;
    margin: 0 7px 5px;
    border-bottom: 2.5px dotted $red;
}